
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body {
  background-color: #DFEFEF;
  -ms-overflow-style: none;
}
body::-webkit-scrollbar {
    display: none !important;
}
































@font-face {
  font-family: 'BMJUA';
  src: url(/static/fonts/BMJUA_ttf.7dbe95a.ttf); }

.Menu {
  position: relative;
  width: 100%;
  margin: 10% auto 5% auto; }

.Menu div, .Menu dt, .Menu dd, .Menu table, .Menu tr, .Menu td, .Menu ul, .Menu ol, .Menu li, .Menu h1, .Menu h2, .Menu h3 {
    padding: 0;
    font-family: 'BMJUA';
    font-size: 10px; }

.Menu div {
    display: block; }

.Menu a {
    color: #c30400;
    text-decoration: none; }

.Menu ul.menu {
    margin: 0 auto 0 auto;
    text-align: center; }

.Menu ul.menu li {
      display: inline-block;
      margin: 1%;
      text-align: center; }

.Menu ul.menu li img.img-resize {
        width: 150px; }

.Clip {
  position: relative;
  width: 100%;
  margin: 0% auto 5% auto; }

.Clip div, .Clip dt, .Clip dd, .Clip table, .Clip tr, .Clip td, .Clip ul, .Clip ol, .Clip li, .Clip h1, .Clip h2, .Clip h3 {
    padding: 0;
    font-family: 'BMJUA';
    font-size: 10px; }

.Clip div {
    display: block; }

.Clip a {
    color: #c30400;
    text-decoration: none; }

.Clip h2 {
    color: #c30400;
    font-size: 3.3em;
    text-align: center;
    margin: 1%; }

.Clip ul.clip {
    margin: 0 auto 0 auto;
    text-align: center; }

.Clip ul.clip li {
      display: inline-block;
      margin: 1%;
      text-align: center; }

.Clip ul.clip li img.img-resize {
        width: 150px; }

.Clip ul.clip #show-next {
      width: 80%;
      font-size: 3em; }

.Clip ul.clip p {
      font-size: 2em;
      margin-top: 0;
      margin-bottom: 0.3em; }

.Playlist {
  position: relative;
  width: 100%;
  margin: 5% auto 5% auto; }

.Playlist div, .Playlist dt, .Playlist dd, .Playlist table, .Playlist tr, .Playlist td, .Playlist ul, .Playlist ol, .Playlist li, .Playlist h1, .Playlist h2, .Playlist h3 {
    padding: 0;
    font-family: 'BMJUA';
    font-size: 10px; }

.Playlist dd {
    background: #fff;
    margin: 1%;
    display: inline-block;
    text-align: center; }

.Playlist div {
    display: block; }

.Playlist a {
    color: #c30400;
    text-decoration: none; }

.Playlist h2 {
    color: #c30400;
    font-size: 3.3em;
    text-align: center;
    margin: 1%; }

.Emoji {
  position: relative;
  width: 100%;
  margin: 5% auto 5% auto; }

.Emoji div, .Emoji dt, .Emoji dd, .Emoji table, .Emoji tr, .Emoji td, .Emoji ul, .Emoji ol, .Emoji li, .Emoji h1, .Emoji h2, .Emoji h3 {
    padding: 0;
    font-family: 'BMJUA';
    font-size: 10px; }

.Emoji div {
    display: block; }

.Emoji a {
    color: #c30400;
    text-decoration: none; }

.Emoji h2 {
    color: #c30400;
    font-size: 3.3em;
    text-align: center;
    margin: 1%; }

.Emoji ul.emoji {
    margin: 0 auto 0 auto;
    text-align: center; }

.Emoji ul.emoji li {
    display: inline-block;
    margin: 1%;
    text-align: center; }
